import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpServiceService } from './../http-service.service';
import { Employee } from './../EmployeeInterface';
import { DialogService } from './../dialog.service';
import { ToastrService } from 'ngx-toastr';
import { LoadingBarService } from '@ngx-loading-bar/core';


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  p:number=1;
  EmpData=[];
  openPopup:boolean;
  selectedId;
  selectedStatus:String;
  empObj:Employee={
    id:0,
    name:"",
    department:"",
    status:"",
    mobileNo:0,
    country:{
        cid:0,
        cname:"",
    },
    createddtm:0,
    createdby:"",
    updateddtm:0,
    updatedby:"",

  };
  isRadioCheck:boolean;
  constructor(private route:Router,
              private service:HttpServiceService,
              private dialogService:DialogService,
              private toastr:ToastrService,
              private loading:LoadingBarService ) { }

  ngOnInit() {
    this.AllEmployee();
  }

  AllEmployee(){
    this.loading.start();
    this.service.getAllEmployee()
    .subscribe((response)=>{
      console.log(response)
      this.EmpData=(<any>response)
      this.loading.stop();
      
    })

  }
  onAddEmployee(){
    this.loading.start();
   this.route.navigate(["/addEmployee"]);
   this.loading.stop();
  }
  onRadioSelect(item){
   this.empObj=item;
   this.isRadioCheck=true;
   this.selectedId=item.id;
   this.selectedStatus=item.status;
  }

  onUpdateEmployee(){
    this.loading.start();
   if(this.isRadioCheck){
    this.openPopup=true;
    this.loading.stop();
   }else{
     alert("please select Radio Button")
     this.loading.stop();
   }
   
  }

  onDeleteEmployee(){
    this.loading.start();
    if(this.isRadioCheck){
      this.dialogService.OpenConfirmDialog('Are you sure to delete this record?')
      .afterClosed()
      .subscribe((res)=>{

        if(res){
          console.log(res);
          this.service.deleteEmployee(this.selectedId)
          .subscribe((response)=>{
            this.AllEmployee();
            this.toastr.success(response)
            this.loading.stop();
          })

        }else{
          console.log(res);
          this.loading.stop();
        }
      })
     }else{
      //  alert("please select Radio Button")
       this.toastr.error("please select Radio Button to Delete")
       this.loading.stop();
     } 
  }

  onChangeStatus(){
    this.loading.start();
    let obj={
      id:this.selectedId,
      status:this.selectedStatus
    }

    if(this.isRadioCheck){
      this.service.changeStatus(obj)
      .subscribe((response)=>{
        console.log(response)
        this.AllEmployee();
        this.toastr.success(response)
        this.loading.stop();  
      })
    }else{
      this.toastr.error("please select Radio Button to Change Status")
      this.loading.stop(); 
    }

  }

}
