import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class HttpServiceService {

  baseUrl:string="http://localhost:8080/api/";

  constructor(private http:HttpClient) { }

  logincheck(obj){
    return (this.http.post(`${this.baseUrl}login`,obj));
  }
  RegisterUser(obj){
    return (this.http.post(`${this.baseUrl}register`,obj,{responseType:'text'}));
  }
  addEmployee(empObj){
    return(this.http.post(`${this.baseUrl}addEmployee`,empObj,{responseType:'text'}));
  }
  getAllCountry(){
    return(this.http.get(`${this.baseUrl}getAllCountry`));
  }
  getAllEmployee(){
    return( this.http.get(`${this.baseUrl}getAllEmployee`));
  }
  getSelectedEmployee(id){
    return(this.http.get(`${this.baseUrl}getSelectedEmployee/${id}`));
  }
  updateEmployee(obj){
    return(this.http.put(`${this.baseUrl}updateEmployee`,obj,{responseType:'text'}));
  }

  deleteEmployee(id){
    return (this.http.delete(`${this.baseUrl}deleteEmployee/${id}`,{responseType:'text'}))
  }

  changeStatus(obj){
    return(this.http.put(`${this.baseUrl}changeStatus`,obj,{responseType:'text'}))
  }
}
