import { Component, OnInit, OnDestroy } from '@angular/core';
import { HttpServiceService } from './../http-service.service';

@Component({
  selector: 'app-add-employee',
  templateUrl: './add-employee.component.html',
  styleUrls: ['./add-employee.component.css']
})
export class AddEmployeeComponent implements OnInit,OnDestroy {

  allCountry=[];
  correct;
  
  constructor(private service:HttpServiceService) { }
  ngOnDestroy(): void {
    document.body.className="";
  }

  ngOnInit() {
    document.body.className="comp-Img";
    this.getAllCountry();
  }

  getAllCountry(){
    this.service.getAllCountry()
    .subscribe((response)=>{
      console.log(response)
      this.allCountry=(<any>response);
    })

  }
  onAddEmployee(f){
   
    
    
    let empObj={
    name:f.value.name,
    department:f.value.department,
    status:f.value.status,
    mobileNo:f.value.mobileNo,
    country:{
      cid:f.value.country.cid,
      cname:f.value.country.cname,
    },
    createddtm:Date.now(),
    createdby:sessionStorage.getItem("username"),
    updateddtm:Date.now(),
    updatedby:sessionStorage.getItem("username"),
      

    }

    this.service.addEmployee(empObj)
    .subscribe((response)=>{
     console.log(response)
     this.correct=response
     f.resetForm();
     
    })
  }

}
